              '_selected'
          "
          :alloy="alloy"
          class="itemAlloy"
          :is-selected="true"
          :color="graphicColors[index]"
          :alloy-from-visualization="true"
        />
        <div v-for="n in 5 - selectedAlloys.length" :key="n" class="empty-alloy-item">
          <div class="dashed"></div>
        </div>
      </div>
    </div>
    <div v-if="tabName === 'graphic'" class="graphic-wrapper">
      <div class="d-flex flex-column">
        <div class="title mb-5 mt-3">
          <img class="filter-arrow mr-3" src="@/assets/filterArrow.png" alt />
          <p class="filter-name text-uppercase">Choose a couple of properties</p>
        </div>
        <!-- switch MPa/Ksi -->
        <div class="switch-header row mx-0 mb-3">
          <span>
            MPa
            <b-form-checkbox
              v-model="selectedUnitBool"
              name="check-button"
              switch
              class="unit-switch"
              @change="selectUnit(selectedUnitBool)"
            ></b-form-checkbox>ksi
          </span>
        </div>
        <!-- couples -->
        <b-form-group>
          <b-form-radio
            v-for="(couple, index) in couplesList"
            :key="index"
            v-model="selected"
            name="some-radios"
            plain
            class="my-radio mb-3"
            :class="selected == index ? 'bold' : null"
            :value="index"
          >
            {{
            couple.prop1.name +
            (couple.prop1.MPaKSI ? ` (${selectedUnit})` : "") +
            " / " +
            couple.prop2.name +
            (couple.prop2.MPaKSI ? ` (${selectedUnit})` : "")
            }}
          </b-form-radio>
        </b-form-group>
        <div class="mt-auto">
          <button
            type="button"
            class="btn btn-primary"
            @click="exportPDF()"
            :disabled="!selectedAlloys.length"
          >Export PDF</button>
          <small v-if="isLoading" class="ml-3 py-2 position-absolute">Generation in progress...</small>
          <small class="d-block mt-2">
