<script>
import Graphic from "@/components/DataVisualization/Graphic.vue";
import GraphicWithCouple from "@/components/DataVisualization/GraphicWithCouple.vue";
import DataSheet from "@/components/DataVisualization/DataSheet.vue";
import Couples from "@/config/couples.js";
import AlloyItem from "@/components/AlloyItem";
import $ from "jquery";
import Store from "@/store";
import VueHtml2pdf from "vue-html2pdf";
import axios from "axios";

export default {
  name: "DataVisualization",
  components: {
    Graphic,
    DataSheet,
    AlloyItem,
    VueHtml2pdf,
    GraphicWithCouple
  },
  props: {
    selectedAlloys: {
      type: Array,
      default: null
    }
  },
  data: () => {
    return {
      selected: 0,
      couplesList: JSON.parse(JSON.stringify(Couples)),
      tabName: "datasheet",
      selectedUnit: "MPa",
      selectedUnitBool: false,
      graphicColors: ["#ade4d9", "#85d6a8", "#43a19b", "#57BFFA", "#B69BDF"],
      activeAlloy: {},
      isVisible: false,
      store: Store.state,
      currentTime: "",
      isLoading: false
    };
  },
  computed: {
    selectedCouple: function() {
      return this.couplesList[this.selected];
    }
  },
  methods: {
    clickOnAlloy(alloy) {
      this.$parent.addSelectedAlloy(alloy);
    },
    selectUnit: function(selection) {
      this.selectedUnit = selection ? "MPa" : "ksi";
    },
    processNumber: function(stringValue) {
      if (!stringValue || stringValue == "-") return "";
      // if format of value is XXX-XXX
      if (/^[\d,]+[^\d,]+[\d,]+$/.test(stringValue))
        return Number.parseInt(stringValue);
      // to get rid of every non digit character except ','
      let digitString = stringValue.replace(/[^\d,]/g, "");
      return Number(digitString.replace(",", "."));
    },
    isActive(alloy) {
      if (
        this.activeAlloy &&
        this.activeAlloy.brandName === alloy.brandName &&
        this.activeAlloy.temper === alloy.temper
      )
        return true;
      return false;
    },
    addActiveAlloy(alloy) {
      if (
        this.activeAlloy.brandName === alloy.brandName &&
        this.activeAlloy.temper === alloy.temper
      )
        this.activeAlloy = {};
      else this.activeAlloy = alloy;
    },
    scroll(direction) {
      var upPos = $(".dataSheet-wrapper").scrollTop();
      let value = 0;
      if (direction === "up") {
        value = upPos - 200;
      } else {
        value = upPos + 200;
      }
      $(".dataSheet-wrapper").animate(
        {
          scrollTop: value
        },
        "fast"
      );
    },
    exportPDF() {
      this.currentTime = new Date().toLocaleString("en-GB", {
        timeZone: "Europe/Paris"
      });
      this.$refs.html2Pdf.generatePdf();

      axios.defaults.baseURL = process.env.VUE_APP_ROOT_API;
      const token = Store.getUserToken();
      const user = Store.state.user;
      if (token) {
        axios.defaults.headers.common["Cockpit-Token"] = token;
        // axios({
        //   method: "post",
        //   url: "collections/save/logs?token=" + user.api_key,
        //   headers: { "Content-Type": "application/json" },
        //   data: JSON.stringify({
        //     data: {
        //       code: "EXP-PDF",
        //       action: "Export PDF",
        //       username: user.name,
        //       user_id: user._id,
        //       date: this.currentTime,
        //       infos: this.selectedAlloys
        //         .map(alloy => `${alloy.brandName} ${alloy.temper}`)
        //         .join(" - ")
        //     }
        //   })
        // }).then(resp => console.log(resp));
      }
    },
    onProgress: function(progress) {
      if (progress == 90) {
        this.isLoading = false;
      }
    },
    hasStartedGeneration: function() {
      this.isLoading = true;
    },
    getName() {
      return (
        "Export LBA - " +
        this.selectedAlloys.map(alloy => alloy.brandName).join("-")
      );
    },
    getOrderOfMagnitude(n) {
      var order = Math.floor(Math.log(n) / Math.LN10 + 0.000000001); // because float math sucks like that
      return Math.pow(10, order);
    }
  }
};
</script>

